@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@400;700&display=swap);
html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
input:focus {
  outline: none;
}
* {
  text-decoration: none;
}

.landing-container {
  width: 100%;
  max-width: 1148px;
  margin: auto;
}
.landing-title {
  width: 100%;
  text-align: center;
  margin-bottom: 18px;
  margin-top: 10px;
  padding: 10px 0px;
  color: rgb(33, 37, 41);
  font-size: 16px;
  font-weight: 700;
  line-height: 24px;
}
.landing-title p {
  font-size: 38px;
  line-height: 42px;
  font-weight: 700;
  color: #4b2272;
}
.landing-wrapper {
  width: 100%;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}
.info-wrapper,
.form-wrapper {
  width: 47%;
  min-width: 400px;
  margin: 12px;
  color: #444444;
  background: rgb(252, 251, 255);
  padding: 30px;
}
.info-wrapper .title {
  font-size: 20px;
  color: #012970;
  font-weight: 700;
  margin: 20px 0 10px 0;
  line-height: 1.2;
}
.info-wrapper p {
  margin-top: 0;
  margin-bottom: 0;
  padding: 0;
  line-height: 24px;
  font-size: 14px;
}
.form-wrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}
.enter-userInfo {
  display: flex;
  flex-direction: row;
  width: 100%;
}
.enter-userInfo input:nth-child(1) {
  margin-right: 10px;
}
.enter-userInfo input:nth-child(2) {
  margin-left: 10px;
}
.form-wrapper .form-control {
  display: block;
  width: 100%;
  padding: 0.375rem 0.75rem;
  font-size: 1rem;
  font-weight: 400;
  line-height: 1.5;
  color: #212529;
  background-color: #fff;
  background-clip: padding-box;
  border: 1px solid #ced4da;
  -webkit-appearance: none;
  -moz-appearance: none;
  appearance: none;
  border-radius: 0.25rem;
  transition: border-color 0.15s ease-in-out, box-shadow 0.15s ease-in-out;
}
.form-wrapper .form-control:focus {
  color: #212529;
  background-color: #fff;
  border-color: #86b7fe;
  outline: 0;
  box-shadow: 0 0 0 0.25rem rgb(13 110 253 / 25%);
}
.enter-message {
  margin-top: 24px;
  width: 100%;
  min-height: calc(1.5em + 0.75rem + 2px);
}
.get-permission {
  margin: 16px;
  margin-left: 0px;
  width: 100%;
  padding-left: 25px;
  position: relative;
}
.get-permission input {
  position: absolute;
  margin: 2px;
  left: 1px;
  top: -1px;
  width: 14px;
  height: 14px;
  background-color: #fff;
  background-repeat: no-repeat;
  background-position: center;
  background-size: contain;
  border: 1px solid rgba(0, 0, 0, 0.25);
}
.get-permission label {
  display: inline-block;
  font-size: 16px;
  line-height: 20px;
}
.submit-wrapper {
  margin-top: 24px;
  display: flex;
  justify-content: center;
  width: 100%;
}
.submit-wrapper button {
  background-color: #7c659e;
  border: 0;
  border-radius: 40px;
  width: 200px;
  height: 45px;
  text-align: center;
  font-size: 1rem;
  color: rgb(255, 255, 255);
  cursor: pointer;
}

@media screen and (max-width: 850px) {
  .landing-title {
    padding: 0px 30px;
  }
  .landing-title p {
    font-size: 30px;
  }
  .info-wrapper,
  .form-wrapper {
    width: calc(100% - 60px);
    margin: 0px 30px;
    margin-bottom: 20px;
    padding: 30px !important;
    min-width: 50px;
  }
}
@media screen and (max-width: 500px) {
  .enter-userInfo {
    flex-direction: column;
  }
  .enter-userInfo input:nth-child(1) {
    margin: 0px;
    margin-bottom: 10px;
  }
  .enter-userInfo input:nth-child(2) {
    margin: 0px;
  }
}

.landing-footer-wrapper {
  width: 100%;
  height: 80px;
  display: flex;
  flex-direction: column;
  padding: 10px 12px;
  margin: 16px 0px;
  background-color: rgb(248, 249, 250);
}
.landing-footer-wrapper div {
  font-size: 16px;
  font-weight: 400;
  height: 24px;
  width: 100%;
  color: #6c757d !important;
  opacity: 0.9;
  display: flex;
  justify-content: center;
  align-items: center;
}
.landing-footer-wrapper div:nth-child(1) {
  margin-bottom: 3px;
}
.landing-footer-wrapper div a {
  font-size: 15px;
  opacity: 0.8;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  font-family: 'Open Sans', sans-serif;
}
html,
body,
#root {
  position: relative;
  width: 100vw;
  height: 100vh;
  display: flex;
  font-family: 'Open Sans', sans-serif;
  flex-direction: column;
}

